.service_container {
  grid-template-columns: repeat(3, 270px);
  justify-content: center;
  column-gap: 1.8rem;
}

.service_content {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--container-color);
  padding: 6rem 0 2rem 2.5rem;
}

.service_icon {
  display: block;
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-1);
}

.service_title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
}

.service_button {
  color: var(--title-color);
  font-size: var(--small-font-size);
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
  cursor: pointer;
}

.service_button-icon {
  font-size: 1rem;
  transition: 0.3s;
}

.service_button:hover .service_button-icon {
  transform: translateX(1rem);
}

.service_modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(0, 0, 0, 0.5);
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}

.service_modal-content {
  width: 500px;
  position: relative;
  background-color: var(--container-color);
  padding: 4.5rem 2.5rem 2.5rem;
  border-radius: 1.5rem;
}

.service_modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 1.5rem;
  color: var(--title-color);
  cursor: pointer;
}

.service_modal-title,
.service_modal-description {
  text-align: center;
}

.service_modal-title {
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1);
}

.service_modal-description {
  font-size: var(--small-font-size);
  padding: 0 3.5rem;
  margin-bottom: var(--mb-2);
}

.service_modal-service {
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
}

.service_modal-icon {
  color: var(--title-color);
  font-size: 1.1rem;
}

.service_modal-info {
  font-size: var(--small-font-size);
}



/*Active Modal*/
.active-modal{
    opacity: 1;
    visibility: visible;
}

@media screen and (max-width:992px) {
    .service_container{
        grid-template-columns: repeat(3,218px);
    }
}

@media screen and (max-width:768px) {
    .service_container{
        grid-template-columns: repeat(2,1fr);
    }

    .service_content{
        padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    }

    .service_icon{
        font-size: 1.5rem;
    }
}

@media screen and (max-width:576px) {
    .service_modal-content{
        padding: 4.5rem 1.5rem 2.5rem;
    }

    .service_modal-description{
        padding: 0;
    }
}

@media screen and (max-width:350px) {
    .service_container{
        grid-template-columns:max-content;
    }   

    .service_content{
        padding-right: 6rem;
    }
}